import { elizaLogger } from "@elizaos/core";
import { SgxAttestationProvider } from "@elizaos/plugin-sgx";
import { RemoteAttestationProvider, type TEEMode } from "@elizaos/plugin-tee";
import { DCAPMode } from "./types";

export const DEFAULT_QUOTE =
    "";

async function tryQuote(promise: Promise<any>, type: string) {
    try {
        const { quote } = await promise;
        return quote as string;
    } catch (e) {
        elizaLogger.error(`Error getting ${type} quote`, e);
    }
}

export async function getQuote(
    message: string,
    dcapMode?: DCAPMode,
    teeMode?: TEEMode
) {
    switch (dcapMode) {
        case DCAPMode.PLUGIN_SGX:
            return await tryQuote(
                new SgxAttestationProvider().generateAttestation(message),
                "SGX"
            );
        case DCAPMode.PLUGIN_TEE:
            return await tryQuote(
                new RemoteAttestationProvider(teeMode).generateAttestation(
                    message
                ),
                "TDX"
            );
    }
    return DEFAULT_QUOTE;
}
